@import "../../commonstyles/flex";
@import "../../commonstyles/variables";

.home {

  height: 100%;
  background: url('./../../img/extras/book-stack.jpg');
  background-size: cover;
  background-position: 0 70%;
  background-attachment: fixed;
  @extend .flex-row;
  @extend .a-c;

  .titleWrapper {
    flex-grow: 1;
    @extend .flex-row;
    @extend .j-c;
    margin-top: -40px;
    .title {
      font-size: 50px;
      text-align: center;
      .first {
        text-shadow: 0 4px 6px #ffffff;
        color: $brandColor;
      }
      .second {
        margin-top: 20px;
        text-shadow: 0 4px 6px #ffffff;
        color: $brandColor;
      }
      .and {
        color: white;
        text-shadow: 0 4px 6px $brandColor;
      }
    }
  }
}

